.typing_loader {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    -webkit-animation: typing 1s linear infinite alternate;
    -moz-animation: typing 1s linear infinite alternate;
    -ms-animation: typing 1s linear infinite alternate;
    animation: typing 1s linear infinite alternate;
    position: relative;
    left: -12px;
    margin: 7px 15px 6px;
  }
  
  .to .typing_loader {
    animation: typing-black 1s linear infinite alternate;
  }
  
  @-webkit-keyframes typing {
    0% {
      background-color: rgba(255, 255, 255, 1);
      box-shadow: 12px 0 0 0 rgba(255, 255, 255, 0.4), 24px 0 0 0 rgba(255, 255, 255, 0.2);
    }
  
    50% {
      background-color: rgba(255, 255, 255, 0.4);
      box-shadow: 12px 0 0 0 rgba(255, 255, 255, 1), 24px 0 0 0 rgba(255, 255, 255, 0.4);
    }
  
    100% {
      background-color: rgba(255, 255, 255, 0.2);
      box-shadow: 12px 0 0 0 rgba(255, 255, 255, 0.4), 24px 0 0 0 rgba(255, 255, 255, 1);
    }
  }
  
  @-moz-keyframes typing {
    0% {
      background-color: rgba(255, 255, 255, 1);
      box-shadow: 12px 0 0 0 rgba(255, 255, 255, 0.4), 24px 0 0 0 rgba(255, 255, 255, 0.2);
    }
  
    50% {
      background-color: rgba(255, 255, 255, 0.4);
      box-shadow: 12px 0 0 0 rgba(255, 255, 255, 1), 24px 0 0 0 rgba(255, 255, 255, 0.4);
    }
  
    100% {
      background-color: rgba(255, 255, 255, 0.2);
      box-shadow: 12px 0 0 0 rgba(255, 255, 255, 0.4), 24px 0 0 0 rgba(255, 255, 255, 1);
    }
  }
  
  @keyframes typing-black {
    0% {
      background-color: rgba(74, 74, 74, 1);
      box-shadow: 12px 0 0 0 rgba(74, 74, 74, 0.4), 24px 0 0 0 rgba(74, 74, 74, 0.2);
    }
  
    50% {
      background-color: rgba(74, 74, 74, 0.4);
      box-shadow: 12px 0 0 0 rgba(74, 74, 74, 1), 24px 0 0 0 rgba(74, 74, 74, 0.4);
    }
  
    100% {
      background-color: rgba(74, 74, 74, 0.2);
      box-shadow: 12px 0 0 0 rgba(74, 74, 74, 0.4), 24px 0 0 0 rgba(74, 74, 74, 1);
    }
  }
  
  @keyframes typing {
    0% {
      background-color: rgba(255, 255, 255, 1);
      box-shadow: 12px 0 0 0 rgba(255, 255, 255, 0.4), 24px 0 0 0 rgba(255, 255, 255, 0.2);
    }
  
    50% {
      background-color: rgba(255, 255, 255, 0.4);
      box-shadow: 12px 0 0 0 rgba(255, 255, 255, 1), 24px 0 0 0 rgba(255, 255, 255, 0.4);
    }
  
    100% {
      background-color: rgba(255, 255, 255, 0.2);
      box-shadow: 12px 0 0 0 rgba(255, 255, 255, 0.4), 24px 0 0 0 rgba(255, 255, 255, 1);
    }
  }
  
  form.convFormDynamic {
    width: calc(100% - 33px);
    margin: 10px auto 15px;
    padding: 0 !important;
    position: relative;
    box-shadow: 0 0 5px 5px rgba(222, 222, 222, 0.4);
  
    textarea.userInputDynamic, input.userInputDynamic {
      border: 0;
      padding: 7px 10px;
      outline: 0;
      font-size: .905rem;
      float: left;
      width: calc(100% - 70px);
      line-height: 1.3em;
      min-height: 1.7em;
      max-height: 10rem;
      display: block;
      max-width: 88%;
      margin-right: 2.5%;
    }
  }
  
  div {
    &.conv-form-wrapper {
      div {
        &#messages {
          max-height: 71vh;
          height: auto !important;
          overflow-y: scroll;
  
          &:after {
            content: '';
            display: table;
            clear: both;
          }
        }
  
        &.wrapper-messages {
          position: relative;
          height: 577px;
          max-height: 71vh;
          overflow-y: scroll;
        }
      }
  
      position: relative;
  
      &:before {
        content: '';
        position: absolute;
        width: 100%;
        display: block;
        height: 30px;
        top: 0;
        left: 0;
        z-index: 2;
        background: linear-gradient(#fff, transparent);
      }
  
      div.wrapper-messages::-webkit-scrollbar {
        width: 0;
        height: 0;
        background: transparent;
      }
    }
  
    &#feed ul::-webkit-scrollbar, &.conv-form-wrapper div.options::-webkit-scrollbar {
      width: 0;
      height: 0;
      background: transparent;
    }
  }
  
  @media (max-width: 767px) {
    div.conv-form-wrapper div {
      &.wrapper-messages, &#messages {
        max-height: 71vh;
      }
    }
  }
  
  input[type="text"].userInputDynamic, textarea.userInputDynamic {
    &.error {
      color: #ac0000 !important;
    }
  
    border-radius: 3px;
    margin: 7px 10px;
  }
  
  div.conv-form-wrapper div {
    &#messages {
      transition: bottom .15s,padding-bottom .15s;
      position: absolute;
      bottom: 0;
      height: auto !important;
      width: 100%;
      padding-bottom: 20px;
    }
  
    &.options {
      word-wrap: normal;
      white-space: nowrap;
      overflow-x: scroll;
      position: absolute;
      bottom: 100%;
      width: 100%;
      transform: translateY(-5px);
  
      &:after {
        content: '';
        display: table;
        clear: both;
      }
  
      div.option {
        padding: 7px 12px;
        border: 1px solid rgba(6, 153, 184, 0.3);
        display: inline-block;
        margin: 5px;
        background: #fff;
        color: #06c5a6;
        cursor: pointer;
        border-radius: 20px;
        font-size: .9rem;
      }
    }
  
    &.message {
      animation: slideTop .15s ease;
  
      &:after {
        content: '';
        display: table;
        clear: both;
      }
  
      &.ready {
        animation: bounceIn .2s ease;
        transform-origin: 0 0;
      }
    }
  
    &#messages div.message {
      border-radius: 20px;
      padding: 12px 22px;
      font-size: .905rem;
      color: #333;
      display: inline-block;
      padding: 10px 15px 8px;
      border-radius: 20px;
      margin-bottom: 5px;
      float: right;
      clear: both;
      max-width: 65%;
      word-wrap: break-word;
  
      &.to {
        background: #efefef;
        color: #6f6f6f;
        float: left;
        border-top-left-radius: 0;
      }
  
      &.from {
        background: #06b79a;
        color: #fff;
        border-top-right-radius: 0;
      }
    }
  }
  
  .message {
    &.to + .message.from, &.from + .message.to {
      margin-top: 15px;
    }
  }
  
  @keyframes slideTop {
    0% {
      margin-bottom: -25px;
    }
  
    100% {
      margin-bottom: 0;
    }
  }
  
  @keyframes bounceIn {
    0% {
      transform: scale(0.75, 0.75);
    }
  
    100% {
      transform: scale(1, 1);
    }
  }
  
  div.conv-form-wrapper div.options div.option {
    &:hover {
      background: #eee;
    }
  
    &.selected {
      background: #06b79a;
      color: #fff;
  
      &:hover {
        background: #069c7f;
      }
    }
  }
  
  form.convFormDynamic button.submit {
    padding: 3px;
    border: 0;
    float: left;
    margin: 5px;
    color: #06c5a6;
    cursor: pointer;
    border-radius: 8px;
    font-size: 1.1rem;
    width: 36px;
    height: 35px;
    margin-top: 4px;
    background: #fff;
    outline: none !important;
  
    &:hover {
      background: #06b79a;
      color: #fff;
    }
  }
  
  button.submit.glow {
    box-shadow: 0 0 10px 5px rgba(6, 197, 166, 0.4);
  }
  
  .no-border {
    border: none !important;
  }
  
  .dragscroll {
    cursor: grab;
  }
  
  div {
    &.conv-form-wrapper div#messages::-webkit-scrollbar, &#feed ul::-webkit-scrollbar {
      width: 0;
      background: transparent;
    }
  }
  
  span.clear {
    display: block;
    clear: both;
  }
  
  .spinLoader {
    background: #06c5a6 !important;
    -webkit-animation: spinLoaderChat 1s infinite ease-in-out;
    animation: spinLoaderChat 1s infinite ease-in-out;
    width: 1em;
    height: 4em;
  
    &:before, &:after {
      background: #06c5a6 !important;
      -webkit-animation: spinLoaderChat 1s infinite ease-in-out;
      animation: spinLoaderChat 1s infinite ease-in-out;
      width: 1em;
      height: 4em;
    }
  
    color: #06c5a6 !important;
    text-indent: -9999em;
    margin: 50% auto;
    position: relative;
    font-size: 11px;
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
  
    &:before, &:after {
      position: absolute;
      top: 0;
      content: '';
    }
  
    &:before {
      left: -1.5em;
      -webkit-animation-delay: -0.32s;
      animation-delay: -0.32s;
    }
  
    &:after {
      left: 1.5em;
    }
  }
  
  @-webkit-keyframes spinLoaderChat {
    0%,80%,100% {
      box-shadow: 0;
      height: 4em;
    }
  
    40% {
      box-shadow: 0 -2em;
      height: 5em;
    }
  }
  
  @keyframes spinLoaderChat {
    0%,80%,100% {
      box-shadow: 0;
      height: 4em;
    }
  
    40% {
      box-shadow: 0 -2em;
      height: 5em;
    }
  }